import Vue from "vue";
import Router from "vue-router";
import HelloWorld from "@/components/HelloWorld";
import showfile from "@/components/showfile";

Vue.use(Router);

export default new Router({
  routes: [
    {
      path: "/",
      name: "HelloWorld",
      component: HelloWorld
    },
    {
      path: "/file/:filename",
      name: "showfile",
      component: showfile
    }
  ]
});



