<template>
  <div>
    <mt-header fixed title="固定在顶部a"> </mt-header>
    <textarea :style="contentStyle" v-model="filecontent"></textarea>
  </div>
</template>

<script>
import axios from "axios";
import { MessageBox } from "mint-ui";
import aesjs from "crypto-js/aes";
import CryptoJS from "crypto-js";
import C from "crypto-js";

let xx = Array.from(new TextEncoder().encode("cszzz111"))
  .map(m => {
    return m.toString(16).padStart(2, "0");
  })
  .join("");

const key = CryptoJS.enc.Utf8.parse(xx); //十六位十六进制数作为密钥
const iv = CryptoJS.enc.Utf8.parse("ABCDEF1234123412"); //十六位十六进制数作为密钥偏移量

//加密方法
function Encrypt(word, key) {
  key = CryptoJS.enc.Utf8.parse(
    Array.from(new TextEncoder().encode(key))
      .map(m => {
        return m.toString(16).padStart(2, "0");
      })
      .join("")
  )
  let srcs = CryptoJS.enc.Utf8.parse(word);
  let encrypted = CryptoJS.AES.encrypt(srcs, key, {
    iv: iv,
    mode: CryptoJS.mode.CBC,
    padding: CryptoJS.pad.Pkcs7
  });
  return encrypted.ciphertext.toString().toUpperCase();
}

//解密方法
function Decrypt(word, key) {
  key = CryptoJS.enc.Utf8.parse(
    Array.from(new TextEncoder().encode(key))
      .map(m => {
        return m.toString(16).padStart(2, "0");
      })
      .join("")
  )
  let encryptedHexStr = CryptoJS.enc.Hex.parse(word);
  let srcs = CryptoJS.enc.Base64.stringify(encryptedHexStr);
  let decrypt = CryptoJS.AES.decrypt(srcs, key, {
    iv: iv,
    mode: CryptoJS.mode.CBC,
    padding: CryptoJS.pad.Pkcs7
  });
  let decryptedStr = decrypt.toString(CryptoJS.enc.Utf8);
  return decryptedStr.toString();
}

function get(url) {
  return new Promise(s => {
    axios.get(url).then(function (m) {
      s(m.data)
    })
  })
}

export default {
  name: "showfile",
  data() {
    return {
      filecontent: "",
      skey: '',
      typeKeyCount: 0
      , contentStyle: `width: 100%;border: none;height: ${screen.availHeight - 80}px;font-family: "lucida grande", "lucida sans unicode", lucida, helvetica, "Hiragino Sans GB", "Microsoft YaHei", "WenQuanYi Micro Hei", sans-serif;`
    };
  },
  methods: {
    setKey: function () {
      return new Promise(s => {
        let msg = ''
        if (this.typeKeyCount <= 0) msg = '请输入密码'
        else if (this.typeKeyCount == 1) msg = '密码错误, 请重新输入'
        else msg = `密码错误, 请重新输入 <br/>the ${this.typeKeyCount} attempt`
        this.typeKeyCount++
        MessageBox.prompt(msg).then(({ value, action }) => {
          localStorage.setItem("commpwd", value);
          s()
        })
      })
    },
    checkKey: function () {
      const ckey = this.getCkey()
      if (!ckey) return false
      if (Encrypt(ckey, ckey) != this.skey)
        return false
      return true
    },
    getCkey: function () {
      return localStorage.getItem("commpwd")
    }
  },
  mounted: async function () {
    let data = await get(`./static/data/${this.$route.params.filename}`)
    // m.data = 密码.密文
    if (!data || data.split('.').length != 2)
      return MessageBox.prompt("数据异常")

    //验证密码
    this.skey = data.split('.')[0]
    while (!this.checkKey()) {
      await this.setKey()
    }

    //得到明文
    const plaintext = Decrypt(data.split('.')[1], this.getCkey())
    console.log(plaintext)
    this.filecontent = plaintext
  }
};
</script>

 <style scoped>
h1,
h2 {
  font-weight: normal;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}
a {
  color: #42b983;
}
</style>



